
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {
name: 'Pages',
components: {
XButton,
Flexbox,
FlexboxItem
},
props: ['startRow', 'pageSize', 'totalCount'],
data() {
return {
disabledPrev: false,
disabledNext: false
};
},
computed: {
totalPage() {
return Math.ceil(this.totalCount / this.pageSize);
},
nowPage() {
return Math.ceil((this.startRow + 1) / this.pageSize) || 1;
}
},
created() {
this.turnPage();
},
methods: {
turnPage(v) {
let startRow;
if (v == 'prev') {
startRow = this.startRow - this.pageSize;
}
if (v == 'next') {
startRow = this.startRow + this.pageSize;
}
if (startRow <= 0) {
startRow = 0;
}
this.$emit('change', startRow);
}
}
};



